        this.step += space
        this.$emit('stepChange', this.step)
        // 判断是否暂停
        if (this.pause) {
          this.pause = false
        } else {
          callback(this.step)
        }
        this.renderScene()
      }, 20)
    },
    renderScene () {
      this.renderer.render(this.scene, this.camera)
    },
    getStep (step) {
      if (step < 0) {
        this.$emit('OpenFinish')
        console.log('动画已播放完毕!')
        return false
      }
      if (step > this.stepCount) {
        // 广播关闭完成事件
        this.$emit('CloseFinish')
        console.log('动画已播放完毕!')
        return false
      }
      this.spiale[0].rotation.set(...this.stepSave[0][step].rotation)
      this.spiale[1].rotation.set(...this.stepSave[1][step].rotation)
      this.spiale[2].rotation.set(...this.stepSave[2][step].rotation)
      this.spiale[3].rotation.set(...this.stepSave[3][step].rotation)
      this.spiale[4].rotation.set(...this.stepSave[4][step].rotation)
      this.meshs[2].position.set(...this.stepSave[2][step].meshs)
      this.spiale[2].position.set(...this.stepSave[2][step].spiale)
      this.meshs[4].position.set(...this.stepSave[4][step].meshs)
      this.spiale[4].position.set(...this.stepSave[4][step].spiale)
      return true
    },
    close (step) {
      if (this.getStep(step)) this.nextStep(2, this.close)
    },
    open (step) {
      if (this.getStep(step)) this.nextStep(-2, this.open)
    },
    dragClose (step) {
      if (this.getStep(step)) this.renderScene()
    },
    dragOpen (step) {
      if (this.getStep(step)) this.renderScene()
    },
    creatMitsubishiColumn (scene, renderer, camera) {
      // 定义长宽都是1平面
      const geometry = new THREE.PlaneGeometry(1, Math.sqrt(3))
      let shape1 = new THREE.Shape()
      shape1.moveTo(0, 0)
      shape1.lineTo(1, 0)
      shape1.lineTo(0.5, Math.sqrt(3) / 2)
      shape1.lineTo(0, 0)
      let triangleGeometry1 = new THREE.ShapeGeometry(shape1)
      // 定义6个颜色
      const colors = ['#64e530', '#ccaa1f', '#6b63ef', '#f6c161', '#f46f4c']
      // 定义6个坐标
      const positions = [[-1, Math.sqrt(3) / 2, 0], [0.5, 2 * Math.sqrt(3) / 4, 0], [1.5, 2 * Math.sqrt(3) / 4, 0], [-0.5, 0, 0], [0, Math.sqrt(3), 0]]
      // 定义6个转轴
      const axiss = [[0.25, 3 * Math.sqrt(3) / 4, 0], [0, 2 * Math.sqrt(3) / 4, 0], [0, 2 * Math.sqrt(3) / 4, 0], [0, 0, 0], [0, 2 * Math.sqrt(3) / 4, 0]]
      // 创造6个平面
      for (let index in colors) {
        // 取得颜色
        const color = colors[index]
        // 取得坐标
        const position = positions[index]
        // 取得转轴
        const axis = axiss[index]
        // 创建转轴
        this.spiale[index] = new THREE.Object3D()
        this.spiale[index].position.set(axis[0], axis[1], axis[2])
        this.scene.add(this.spiale[index])
        if (index === '3') {
          this.meshs[index] = new THREE.Mesh(triangleGeometry1, new THREE.MeshPhongMaterial({
            color: color,
            transparent: true,
            // 双面双面贴图
            side: THREE.DoubleSide
          }))
        } else if (index === '4') {
          this.meshs[index] = new THREE.Mesh(triangleGeometry1, new THREE.MeshPhongMaterial({
            color: color,
            transparent: true,
            // 双面双面贴图
            side: THREE.DoubleSide
          }))
        } else {
          this.meshs[index] = new THREE.Mesh(geometry, new THREE.MeshPhongMaterial({
            color: color,
            transparent: true,
            // 双面双面贴图
            side: THREE.DoubleSide
          }))
        }
        // 设置平面位置
        this.meshs[index].position.set(position[0], position[1], position[2])
        // 设置平面角度
        this.meshs[index].rotation.set(0, 0, 0)
        // 设置平面缩放
        this.meshs[index].scale.set(1, 1, 1)
        // 取消面剔除
        this.meshs[index].doubleSided = true
        this.meshs[index].castShadow = true
        // 将平面添加到场景中
        this.scene.add(this.meshs[index])
        this.spiale[index].add(this.meshs[index])
      }
      // this.spiale[4].add(new THREE.AxesHelper(50))
      setTimeout(() => {
        this.spiale[0].rotation.z = 60 * (Math.PI / 180)
        this.spiale[1].rotation.z = -60 * (Math.PI / 180)
        this.spiale[2].rotation.z = -60 * (Math.PI / 180)
        this.spiale[4].rotation.z = -60 * (Math.PI / 180)
        this.renderScene()
      }, 1)
    }
  }
}
</script>



