:[1.8325957145940461,0,0]},{"rotation":[1.8500490071139892,0,0]},{"rotation":[1.8675022996339325,0,0]},{"rotation":[1.8849555921538759,0,0]},{"rotation":[1.9024088846738192,0,0]},{"rotation":[1.9198621771937625,0,0]},{"rotation":[1.9198621771937625,0,0]}]]
