tion":[-0.872664625997166,0,0]},{"rotation":[-0.8901179185171093,0,0]},{"rotation":[-0.9075712110370526,0,0]},{"rotation":[-0.925024503556996,0,0]},{"rotation":[-0.9424777960769393,0,0]},{"rotation":[-0.9599310885968826,0,0]},{"rotation":[-0.9773843811168258,0,0]},{"rotation":[-0.9948376736367692,0,0]},{"rotation":[-1.0122909661567123,0,0]},{"rotation":[-1.0297442586766556,0,0]},{"rotation":[-1.047197551196599,0,0]},{"rotation":[-1.0646508437165423,0,0]},{"rotation":[-1.0821041362364858,0,0]},{"rotation":[-1.0995574287564294,0,0]},{"rotation":[-1.1170107212763727,0,0]},{"rotation":[-1.1344640137963158,0,0]},{"rotation":[-1.151917306316259,0,0]},{"rotation":[-1.1693705988362022,0,0]},{"rotation":[-1.186823891356146,0,0]},{"rotation":[-1.2042771838760893,0,0]},{"rotation":[-1.2217304763960324,0,0]},{"rotation":[-1.239183768915976,0,0]},{"rotation":[-1.256637061435919,0,0]},{"rotation":[-1.2740903539558626,0,0]},{"rotation":[-1.2915436464758059,0,0]},{"rotation":[-1.3089969389957494,0,0]},{"rotation":[-1.3264502315156927,0,0]},{"rotation":[-1.3439035240356358,0,0]},{"rotation":[-1.361356816555579,0,0]},{"rotation":[-1.3788101090755225,0,0]},{"rotation":[-1.3962634015954658,0,0]},{"rotation":[-1.4137166941154091,0,0]},{"rotation":[-1.4311699866353527,0,0]},{"rotation":[-1.4486232791552958,0,0]},{"rotation":[-1.4660765716752389,0,0]},{"rotation":[-1.4835298641951822,0,0]},{"rotation":[-1.5009831567151257,0,0]},{"rotation":[-1.518436449235069,0,0]},{"rotation":[-1.5358897417550126,0,0]},{"rotation":[-1.553343034274956,0,0]},{"rotation":[-1.5707963267948994,0,0]},{"rotation":[-1.5882496193148428,0,0]},{"rotation":[-1.6057029118347863,0,0]},{"rotation":[-1.6231562043547296,0,0]},{"rotation":[-1.640609496874673,0,0]},{"rotation":[-1.6580627893946163,0,0]},{"rotation":[-1.6755160819145598,0,0]},{"rotation":[-1.6929693744345031,0,0]},{"rotation":[-1.7104226669544464,0,0]},{"rotation":[-1.72787595947439,0,0]},{"rotation":[-1.745329251994333,0,0]},{"rotation":[-1.745329251994333,0,0]}]]
